import React, { useState, useEffect } from 'react'
import Search from "Component/Search";
import Pagination from './Pagination'
import UMEdit from './UMEdit'
import { useSelector, useDispatch } from 'react-redux';
import { getUserList } from 'apiServices'
import { getUserRoles, getInventoryModules } from 'apiServices/dropdown'
import Saved from './Saved';

const UserManagement = () => {

    const status = useSelector(state => state.status)
    const thereAreRequest = useSelector(state => state.thereAreRequest)
    const [search, setSearch] = useState('')
    const [userList, setUserList] = useState([])
    const [user, setUser] = useState()

    const dispatch = useDispatch()
    useEffect(() => {
        getUserRoles({ dispatch })
        getInventoryModules({ dispatch })
        getUserList(setUserList)

    }, []);
    return (
        <div className="w-100  stock-take-summary">
            {user ? <UMEdit setUser={setUser} user={user} /> : null}
            <Saved user={user} setUser={setUser} status={status} />
            <div className="py-3 pl-3 pr-3 bg-white border-top border-right border-left radius-top-left radius-top-right">
                <Search
                    hideFilter={true}
                    moduleName={'userManagement'}
                    placeholder={"Enter User"}
                    filter={false}
                    searchHandler={(desc) => null}
                />
            </div>
            <div className='bg-white border-bottom border-right border-left radius-bottom-left radius-bottom-right'>
                <Pagination userList={userList} setUser={setUser} thereAreRequest={thereAreRequest} />
            </div>
        </div>
    )
}

export default UserManagement